import Header from "../../UI/Header";
import classes from "./About.module.css";
import Card from "../../UI/Card";
// import Identify from "../../../store/name-context";
// import { useContext } from "react";
const About = () => {
  return (
    <div className={classes["about-content"]}>
      <Header title="About me" />
      <hr className={classes.hr} />
      <div className={classes.skills}>
        <Card>
          <h3>Programming Languages</h3>
          <hr className={classes.subhr} />
          <i>
            <p>Python</p>
            <p>C++</p>
            <p>MATLAB</p>
          </i>
        </Card>

        <Card>
          <h3>Web Languages</h3>
          <hr className={classes.subhr} />
          <i>
            <p>HTML</p>
            <p>CSS</p>
            <p>JavaScript</p>
            <p>SQL</p>
          </i>
        </Card>
        <Card>
          <h3>Web Libraries and Tech</h3>
          <hr className={classes.subhr} />
          <i>
            <p>React.js</p>
            <p>Sass, Material UI</p>
            <p>PostgreSQL</p>
          </i>
        </Card>

        <Card>
          <h3>AI Libraries</h3>
          <hr className={classes.subhr} />
          <i>
            <p>Tensor Flow</p>
            <p>Numpy</p>
            <p>Pandas</p>
            <p>Matplotlib</p>
          </i>
        </Card>

        <Card>
          <h3>Familiar with</h3>
          <hr className={classes.subhr} />
          <i>
            <p>Prolog</p>
            <p>Octave</p>
            <p>Verilog</p>
            <p>Vue.js</p>
            <p>SQLite</p>
            <p>Node.js</p>
          </i>
        </Card>
        <Card>
          <h3>Others</h3>
          <hr className={classes.subhr} />
          <i>
            <p>• Applications of ML and CNNs </p>
            <p>• Programming ARM microcontroller (STM32)</p>
            <p>• Web application and website development</p>
            <p>• Git, VS code, Pycharm</p>
          </i>
        </Card>
        <Card>
          <h3>OS</h3>
          <hr className={classes.subhr} />
          <i>
            <p>Ubuntu</p>
            <p>Windows</p>
          </i>
        </Card>
      </div>
    </div>
  );
};

export default About;
