import "./App.css";
import Context from "./components/Context/Context";
import ButtonList from "./components/ButtonList/ButtonList";
import { useState } from "react";
import Identify from "./store/name-context";
import MenuListComposition from "./components/UI/Navbar";
import './App.css';

function App() {
  const [page, setPage] = useState("HOME");
  const pageHandler = (pageName) => {
    setPage(pageName);
    console.log(pageName);
  };

  return (
    <Identify.Provider value={{ name: "Arash" }}>
      <div className="App">
        <div className="my-nav">
          <MenuListComposition />
        </div>

        <Context page={page} />
        <ButtonList pageHandler={pageHandler} />
      </div>
    </Identify.Provider>
  );
}

export default App;
